.contact{
    display: flex;
    height: 70vh;
    margin-top: 6rem;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 10rem;
}

.contact>:nth-child(1){
    color: orange;
    font-size: 2.5rem;
    font-weight: bold;
}
.contact>:nth-child(2){
    color: white;
    font-size: 2.5rem;
    font-weight: bold;
}

@media screen and (max-width: 480px) {
    .contact>:nth-child(2){
        color: white;
        font-size: 1.5rem;
        font-weight: bold;
    }

}