.Pcard{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 32rem;
    height: 22rem;
    background: rgba(255, 255, 255, 0.26);
    border: 6px solid orange;
    box-shadow: yellow;
    border-radius: 20px;
    position: relative;
    gap: 1rem;
}

/* 프로젝트 상세 */
.card-back{
    /* width: 32rem;
    height: 22rem; */
    background: rgb(211, 162, 0, 0.7);
    /* position: relative;
    align-items: center;
    justify-content: center;
    display: flex;
