    gap: 1rem;
    border-radius: 10px;
    word-break:break-all; */
}

.Pcard>img{
    width: 32rem;
    height: 18rem;
    border-radius: 10%;

}

.Pcard>span{
    color: white;
}


/* 화면이 작을 때*/
@media screen and (max-width: 480px){
    .Pcard{
        width: 70%;
    }
    .Pcard>img{
        width: 12rem;
        height: 6.5rem;
    }
    .card-back{
        width: 70%;
        font-size: 1rem;
    }
    .more{
        display: none;
    }
}


/* 상세보기 버튼 */
.more {
    border: none;
    background: orange;
    font-family: DNFForgedBlade;
    color: black;
    border-radius: 10px;
}

/* 버튼 호버 이펙트 */
.more:hover{
    cursor: pointer;
}


/* 상세 내용 폰트 */
pre{
    font-family: DNFForgedBlade;
    line-height: 170%;
    font-size: 1rem;
    font-weight: lighter;
}

.back{
    margin-left:-100%;
    border: none;
    background: none;
}

.back:hover{
    cursor: pointer;
}